.signup_container {
	width: 100%;
	min-height: 100vh;
	background-image: linear-gradient(to left,brown,rgb(66, 66, 52),rgb(2, 2, 2));
	display: flex;
	align-items: center;
	justify-content: center;
}

.signup_form_container {
	height:50vh;
	width:50vw;
	display: flex;
	border-radius: 10px;
	box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 20%),
		0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%);
}

.right {
	flex: 2;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-image: linear-gradient(to left,rgb(95, 76, 182),rgb(189, 189, 55),rgb(165, 150, 150));
	border-top-left-radius: 10px;
	border-bottom-left-radius: 10px;
}

.form_container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.form_container h1 {
	top:30vh;
	color: red;
	font-size: 25px;
}

.input {
	outline: none;
	border: none;
	width: 40vw;
	padding: 10px;
	border-radius: 10px;
	background-color: #edf5f3;
	margin: 5px 0;
	font-size: 14px;
}

.error_msg {
	width: 370px;
	padding: 15px;
	margin: 5px 0;
	font-size: 14px;
	background-color: #f34646;
	color: white;
	border-radius: 5px;
	text-align: center;
}

.green_btn {
	border: none;
	outline: none;
	padding: 12px 0;
	background-color: white;
	border-radius: 20px;
	width: 180px;
	font-weight: bold;
	font-size: 14px;
	cursor: pointer;
	background-color: #3bb19b;
	color: white;
	margin: 10px;
}

.white_btn {
	border: none;
	outline: none;
	padding: 12px 0;
	background-color: rgb(128, 126, 126);
	border-radius: 20px;
	width: 180px;
	font-weight: bold;
	font-size: 14px;
	cursor: pointer;
	color: white;
	margin: 10px;
}